import React from 'react'
import { graphql } from 'gatsby'
import Layout from '../layout'
import withRoot from '../../withRoot'
import Paper from '../utils/paper'
import ScrollProgress from '../utils/scroll-progress'
import { ColorlessTag, ColorlessCat } from '../utils/hash-colorful-tag'
import { Helmet } from "react-helmet"
import 'prismjs/themes/prism-tomorrow.css'
import 'prismjs/plugins/line-numbers/prism-line-numbers.css'
import 'katex/dist/katex.min.css'
import '../../static/css/gist.css'
import MyValine from '../utils/add-valine'
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';

class BlogPost extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    // hashhash 
    let hash = window.location.hash
    if (hash) {
      let blockID = hash.split('#')[1]
      let block = document.querySelector(`div[data-block-id="${blockID}"]`)
      if (block) {
        window.setTimeout(() => {
          block.style.background = 'rgba(45, 170, 219, 0.3)'
          block.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" })
        }, 1000);
      }
    }
  }

  render() {
    const { data } = this.props
    let siteConfig = data.siteConfig
    const { public_date,
      status,
      name,
      tags,
      categories,
      html
    } = data.posts
    const seoKeywords = tags ? tags.join(" ") : 'gatsby blog notion'
    return (
      <div>
        <ScrollProgress />
        <Layout navStyle={{ background: 'rgba(0,0,0,.05)' }} wrapStyle={{ marginTop: 0 }}>
          <div style={{
            width: '100%',
            height: '150px',
            zIndex: 1,
            display: 'inline-block'
          }} />
          <Helmet defaultTitle={`${siteConfig.title} - ${name}`}>
            <meta name="description" content={`${seoKeywords} ${name}`} />
          </Helmet>
          <main style={{
            maxWidth: 900,
            margin: '0 auto',
            marginTop: -100,
            marginBottom: 100,
            zIndex: 10
          }} className="sticky">
            {status == "published" &&
              <div role='meta' style={{ display: 'flex' }}>
                <div style={{
                  background: '#eee',
                  color: '#000',
                  display: 'flex',
                  alignItems: 'center',
                  flexShrink: 0,
                  height: '24px',
                  borderRadius: '3px',
                  paddingLeft: '8px',
                  paddingRight: '8px',
                  fontSize: '16px',
                  lineHeight: '24px',
                  fontWeight: '400',
                  margin: '0px 6px 6px 0px',
                }}>{public_date}</div>
                <div className="categories">
                  <FolderOpenIcon style={{
                    color: '#333399',
                    fontSize: '24px',
                    lineHeight: '100%',
                    height: '24px',
                  }} />
                  <span>{categories && categories.map(category => <ColorlessCat category={category} key={category} />)}</span></div>
                <LocalOfferIcon style={{
                  color: 'rgb(255, 56, 96)',
                  fontSize: '24px',
                  lineHeight: '100%',
                  height: '24px',
                }} />
                {tags && tags.map(tag => <ColorlessTag tag={tag} key={tag} />)}
              </div>}
            <Paper>
              <h1>{name}</h1>
              <div dangerouslySetInnerHTML={{ __html: html }} />
            </Paper>
            <MyValine />
          </main>
        </Layout>
      </div>
    )
  }
}

export default withRoot(BlogPost)

export const query = graphql`
  query($slug: String!) {
    siteConfig {
        title
        commentOpen
    }
    posts(slug: { eq: $slug } ) {
        public_date
        status
        description
        name
        tags
        categories
        html
        slug
    }
  }
`