import React, { Component } from 'react'
import ScrollProgress from 'scrollprogress'
import Slider from '@material-ui/core/Slider'

import IconButton from '@material-ui/core/IconButton'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
export default class ScrollProgressComponent extends Component {

  constructor(props) {
    super(props)
    this.state = {
      progress: 0,
    }
  }

  back2Top = () => {
    let top = document.getElementById("top")
    top.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" })
  }

  componentDidMount() {
    this.progressObserver = new ScrollProgress((x, y) => {
      this.setState({ progress: y * 100 })
    })
  }

  componentWillUnmount() {
    this.progressObserver.destroy()
  }

  render() {
    const style = {
      height: '2px',
      position: 'fixed',
      bottom: 0,
      // top: 0,
      zIndex: 100,
      paddingBottom: 0,
    }

    return (
      <div>
        <Slider
          style={style}
          value={this.state.progress}
          min={0}
          max={100}
          thumb={<span />}
        /> {(this.state.progress > 3) && <IconButton className="bak2top" onClick={this.back2Top} style={{
          background: "rgba(0, 0, 0, 0.1)",
          position: "fixed",
          right: 30,
          bottom: 50,
          zIndex: 99,
        }}><ArrowUpwardIcon /> </IconButton>}
      </div>
    )
  }
}
